@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: 'Roboto';
  overflow-x: hidden; }

a {
  text-decoration: none; }

button {
  border-radius: 5px;
  padding: 5px 15px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  -webkit-transition-duration: 0.4s;
          transition-duration: 0.4s;
  cursor: pointer; }

.navbar {
  background-color: #dadada;
  padding: 5px 15px;
  width: 100%;
  z-index: 2;
  position: fixed; }
  .navbar h1 {
    display: inline-block;
    margin: 0 40px;
    font-size: 20px; }
  .navbar .buttons {
    display: inline-block; }
    .navbar .buttons button {
      background-color: silver;
      color: black;
      border: 1px solid #707070; }
      .navbar .buttons button:hover, .navbar .buttons button.current {
        background-color: #707070;
        color: white; }
  @media only screen and (max-width: 655px) {
    .navbar .buttons,
    .navbar h1 {
      display: block;
      text-align: center; } }

.card {
  position: relative;
  background-color: bisque;
  width: 230px;
  border-radius: 10px;
  margin: 5px auto; }
  .card .text {
    display: none;
    position: absolute;
    background-color: rgba(255, 228, 196, 0.596);
    height: 340px;
    padding: 10px;
    overflow: hidden; }
    .card .text .options {
      display: grid;
      grid-template-columns: 65% 30%; }
      .card .text .options button {
        background-color: #e4e4e4;
        color: #5f5f5f;
        font-size: 16px;
        border: 0px solid #cacaca; }
        .card .text .options button:hover, .card .text .options button.current {
          background-color: #cacaca;
          color: #070707; }
      .card .text .options .like {
        display: inline-block;
        cursor: pointer; }
        .card .text .options .like i {
          margin: 0 auto;
          font-size: 30px;
          color: #a30000; }
  .card:hover .text {
    display: block; }
  .card .image {
    width: 230px;
    height: 340px;
    background-size: cover !important; }
  .card p.movietitle {
    font-size: 19px;
    cursor: pointer;
    text-align: center; }
  @media only screen and (max-width: 1170px) {
    .card {
      width: 180px; }
      .card .text {
        height: 240px; }
      .card .image {
        width: 180px;
        height: 260px; } }

.welcome {
  min-height: 120vh;
  background-color: #2b2b2b;
  padding: 50px; }
  .welcome .banner {
    display: grid;
    grid-template-columns: 20% 80%;
    background-color: #a5a5a5;
    padding: 20px; }
    .welcome .banner h2 {
      font-size: 40px; }
    .welcome .banner img {
      margin: auto;
      width: 100px;
      left: 50px; }
  .welcome .main .search {
    margin: 0 auto;
    background-color: #6e6e6e;
    padding: 10px; }
    .welcome .main .search input {
      background: rgba(255, 255, 255, 0);
      height: 35px;
      font-size: 30px;
      border: 0px solid #cacaca;
      border-radius: 5px;
      color: white; }
      .welcome .main .search input::-webkit-input-placeholder {
        color: #aaaaaa; }
      .welcome .main .search input::-moz-placeholder {
        color: #aaaaaa; }
      .welcome .main .search input:-ms-input-placeholder {
        color: #aaaaaa; }
      .welcome .main .search input::-ms-input-placeholder {
        color: #aaaaaa; }
      .welcome .main .search input::placeholder {
        color: #aaaaaa; }
    .welcome .main .search button {
      background-color: #969696;
      color: #cecece;
      font-size: 16px;
      border: 0px solid #cacaca; }
      .welcome .main .search button:hover, .welcome .main .search button.current {
        background-color: #cacaca;
        color: #070707; }
  .welcome .main .list {
    background-color: #b1b1b1;
    min-height: 100vh;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr; }
  @media only screen and (max-width: 936px) {
    .welcome {
      padding: 50px 0 0 0; }
      .welcome .main .list {
        grid-template-columns: 1fr 1fr 1fr; } }
  @media only screen and (max-width: 634px) {
    .welcome .main .list {
      grid-template-columns: 1fr 1fr; } }
  @media only screen and (max-width: 390px) {
    .welcome {
      padding: 70px 0 0 0; }
      .welcome .main .list {
        grid-template-columns: 1fr; } }

.MovieDetail {
  min-height: 120vh;
  background-color: #2b2b2b;
  padding: 50px; }
  .MovieDetail h2 {
    margin: 0; }
  .MovieDetail .banner {
    display: grid;
    grid-template-columns: 20% 80%;
    background-color: #a5a5a5;
    padding: 20px; }
    .MovieDetail .banner h2 {
      font-size: 40px; }
    .MovieDetail .banner img {
      margin: auto;
      width: 50px;
      left: 50px; }
    .MovieDetail .banner p.tagline {
      font-size: 20px;
      margin: 0; }
  .MovieDetail .detail {
    background-color: #e2e2e2;
    height: 325px;
    display: grid;
    grid-template-columns: 20% 80%; }
    .MovieDetail .detail .poster {
      background-size: cover !important;
      background-position: center !important;
      width: 100%;
      height: 325px; }
    .MovieDetail .detail .movieBackground {
      background-size: cover !important;
      width: 100%;
      height: 325px;
      background-position: center top !important; }
  .MovieDetail .summary {
    min-height: 50vh;
    background-color: #e2e2e2;
    padding: 20px 200px; }
    .MovieDetail .summary h3,
    .MovieDetail .summary h4 {
      margin: 0; }
    .MovieDetail .summary p {
      margin: 5px; }
    .MovieDetail .summary .fact * {
      display: inline-block; }
  .MovieDetail .critics {
    padding: 20px 0; }
    .MovieDetail .critics h3 {
      padding: 0 0 20px 0; }
    .MovieDetail .critics p {
      margin: 0;
      font-size: 14px;
      padding: 6px 0; }
      .MovieDetail .critics p.showmore {
        cursor: pointer;
        font-weight: bold;
        font-size: 12px; }
  @media only screen and (max-width: 936px) {
    .MovieDetail {
      padding: 50px 0 0 0; }
      .MovieDetail .summary {
        padding: 20px 100px; } }
  @media only screen and (max-width: 655px) {
    .MovieDetail {
      padding: 60px 0 0 0; }
      .MovieDetail .detail {
        grid-template-columns: 25% 65%; }
      .MovieDetail .summary {
        padding: 20px 50px; } }
  @media only screen and (max-width: 390px) {
    .MovieDetail {
      padding: 75px 0 0 0; }
      .MovieDetail .detail {
        grid-template-columns: 30% 70%; }
      .MovieDetail .summary {
        padding: 20px 30px; } }

